@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);

/* import google font */


/* general CSS */
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: sans-serif;
}

body {
    background-image: linear-gradient(to bottom, rgb(206, 204, 204), rgb(168, 159, 159));
}

.height-container {
    min-height: 100vh;
}

/* Header and Navigation */
.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    background-color: #EA4E4E;
    border-bottom: 1px solid black;
    box-shadow: 0 4px 2px -2px black;
    margin-bottom: 50px;
}

.left-side-nav {
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-name {
    font-family: 'Great Vibes', cursive;
    font-size: 3rem;
}

.header-name:hover {
    cursor: pointer;
}

.right-side-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar-list-item {
    margin-left: 15px;
    margin-right: 15px;
    font-weight: bold;
}

.navbar-list-item:hover {
    cursor: pointer;
    scale: 1.05;
    transition-duration: 100ms;
}

/* Footer */
.footer {
    margin-top: 50px;
    padding: 20px 20px;
    background-color: #EA4E4E;
    border-top: 1px solid black;
    box-shadow: 0px -2px 2px black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-link {
    color: black;
}

.footer-icon {
    margin-left: 10px;
    margin-right: 10px;
}

.footer-icon:hover {
    scale: 1.1;
    transition-duration: 100ms;
    cursor: pointer;
}

/* contact section */
.contact-section {
    width: 40vw;
    background-color: #EA4E4E;
    margin-left: auto;
    margin-right: auto;
    padding: 30px;
    border-radius: 5px;
    border: 2px solid black;
}

.form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.contact-title {
    font-size: 2.4rem;
    font-weight: bold;
    margin-bottom: 15px;
}

.contact-text {
    margin-bottom: 10px;
}

#contact-button, #contact-message, #contact-email, #contact-name {
    width: 100%;
    border-radius: 5px;
    border: 2px solid black;
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 10px 5px;
    background-color: rgb(206, 204, 204);
}

#contact-message::-webkit-input-placeholder, #contact-email::-webkit-input-placeholder, #contact-name::-webkit-input-placeholder {
    color: rgb(27, 25, 25);
}

#contact-message::placeholder, #contact-email::placeholder, #contact-name::placeholder {
    color: rgb(27, 25, 25);
}

#contact-button:hover {
    cursor: pointer;
    scale: 1.05;
    transition-duration: 100ms;
}

.form-err-message {
    color: black;
    font-weight: bold;
    align-self: flex-start;
    margin-bottom: 5px;
}

.swal2-popup {
    background-color: #EA4E4E;
    border: 2px solid black;
}

.swal2-title {
    color: black;
}

.swal2-confirm {
    background-color: rgb(206, 204, 204) !important;
    color: black !important;
    border: 2px solid black !important;
    border-radius: 5px !important;
}

.swal2-confirm:hover {
    scale: 1.05 !important;
    transition-duration: 100ms !important;
}

/* about me section */
.about-me-section {
    width: 80vw;
    background-color: #EA4E4E;
    margin-left: auto;
    margin-right: auto;
    padding: 30px;
    border-radius: 5px;
    border: 2px solid black;
    display: flex;
    align-items: center;
    justify-content: space-between;    
}

.about-me-left-side {
    margin-right: 30px;
}

.hero-image {
    height: 400px;
    border-radius: 5px;
    border: 2px solid black;
}

#about-me-title {
    font-size: 2.4rem;
    font-weight: bold;
    margin-bottom: 30px;
}

/* portfolio section */
.portfolio-title {
    font-size: 2.4rem;
    font-weight: bold;
    margin-bottom: 30px;
    text-align: center;
}

.portfolio-section {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    gap: 20px;
    justify-items: center;
    margin-bottom: 80px;
}

.project {
    display: inline-block;
    background-color: black;
    width: 25vw;
    height: 25vw;
    border-radius: 5px;
    border: 2px solid black;
}

.project-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.6;
    border-radius: 5px;
}

.project-container {
    padding: 15px 10px 15px 10px;
    background-color: #EA4E4E;
    border: 2px solid black;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.demo-code-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 10px;
}

.project-code {
    margin-left: 5px;
}

.project-demo {
    margin-right: 5px;
}

.project-title {
    text-align: center;
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 1.2rem;
}

.project-code, .project-demo {
    text-decoration: none;
    color: black;
    padding: 10px 20px;
    background-color: rgb(206, 204, 204);
    border-radius: 5px;
    border: 2px solid black;
}

.project-code:hover, .project-demo:hover {
    scale: 1.05;
    transition-duration: 100ms;
}

/* resume section */
.resume-section {
    background-color: #EA4E4E;
    width: 60vw;
    margin-left: auto;
    margin-right: auto;
    padding: 30px;
    margin-top: 20px;
    margin-bottom: 20px;
    border: 2px solid black;
    border-radius: 5px;
}

.tech-list-item {
    margin: 15px;
    margin-left: 18px;
}

.resume-title {
    font-size: 2.4rem;
    margin-bottom: 20px;
    font-weight: bold;
}

.download-resume-text {
    font-size: 1.3rem;
    margin-bottom: 40px;
}

.tech-list-title {
    font-size: 1.3rem;
    font-weight: bold;
}

/* responsive design */
@media only screen and (max-width: 697px){
    .about-me-section {
        flex-direction: column;
    }

    #about-me-title, #about-me-paragraph {
        text-align: center;
    }

    #about-me-paragraph {
        margin-bottom: 30px;
    }
}

@media only screen and (max-width: 640px) {
    .navbar {
        flex-direction: column;
    }

    .navbar-list-item {
        font-size: 0.8rem;
    }

    .right-side-nav {
        margin-top: 10px;
    }

    .contact-title {
        font-size: 1.5rem;
        text-align: center;
    }

    .contact-text {
        text-align: center;
    }
}

@media only screen and (max-width: 980px) {
    .portfolio-section {
        grid-template-columns: repeat(2, 1fr);
    }

    .project-title {
        font-size: 1rem;
    }
}
